<template>
  <div class="login">
    <h1 class="pf-medium"></h1>
    <VanForm ref="loginForm" @submit="submit">
      <VanField
        v-model="phoneNumber"
        name=""
        type="tel"
        label=""
        placeholder=""
        :rules="[
          {
            pattern: phonePattern,
            message: ''
          }
        ]"
        @update:model-value="phoneUpdate"
      />
      <VanField
        v-model="password"
        type="password"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <VanField
        v-if="loginText == ''"
        v-model="phoneCode"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      >
        <template #button>
          <div class="send-btn" @click="sendClick">{{ sendText }}</div>
        </template></VanField
      >
      <div style="margin: 16px">
        <VanButton
          class="submit-btn"
          round
          block
          type="primary"
          native-type="submit"
          :color="loginText == '' ? '#ff5900' : ''"
        >
          {{ loginText }}
        </VanButton>
      </div>
    </VanForm>
  </div>
</template>
<script lang="ts" setup>
import { Ref, ref } from 'vue'
import {
  Form as VanForm,
  Field as VanField,
  Button as VanButton,
  Toast
} from 'vant'
import { auth, localStore } from '@/utils'
import { STORAGE_KEYS } from '@/constant/storage'
import { checkUserExist } from '@/api/common'
import { useRoute } from 'vue-router'

const phoneNumber = ref('')
const phonePattern = ref(/^1[0-9]{10}$/)
const password = ref('')
const phoneCode = ref('')
const sendText = ref('')
const loginText = ref('')

const phoneUpdate = (val: string) => {
  if (val.length == 11) {
    checkUserExist(val).then((res) => {
      if (res) {
        loginText.value = ''
      } else {
        loginText.value = ''
      }
    })
  }
}

let sendTime = localStore.getItem(STORAGE_KEYS.SEND_TIME)
const nowTime = Date.now()
if (sendTime) {
  let diffTime = nowTime - sendTime
  if (diffTime < 60 * 1000) {
    countDown(Math.floor(diffTime / 1000))
  }
}

function countDown(time: number) {
  sendText.value = `${time}s`
  const interval = setInterval(() => {
    sendText.value = `${--time}s`
    if (time <= 0) {
      clearInterval(interval)
      sendText.value = ''
    }
  }, 1000)
}

const loginForm: Ref<any> = ref(null)
const sendClick = () => {
  if (sendText.value == '' && loginForm.value) {
    loginForm.value.validate('').then(() => {
      sendTime = Date.now()
      localStore.setItem(STORAGE_KEYS.SEND_TIME, sendTime)
      countDown(60)
      // 
      auth.sendPhoneCode(phoneNumber.value)
    })
  }
}

const route = useRoute()
const submit = () => {
  function goBack() {
    const referrer =
      route.query.referrer || document.referrer || '//m.wzmxx.com'
    location.href = decodeURIComponent(referrer.toString())
  }
  if (loginText.value == '') {
    auth
      .signInWithPhoneCodeOrPassword({
        phoneNumber: phoneNumber.value,
        password: password.value
      })
      .then(() => {
        Toast('')
        setTimeout(() => {
          goBack()
        }, 1000)
      })
      .catch(() => {
        Toast(`\n/`)
      })
  } else if (loginText.value == '') {
    auth
      .signUpWithPhoneCode(phoneNumber.value, phoneCode.value, password.value)
      .then(() => {
        Toast('')
        setTimeout(() => {
          goBack()
        }, 1000)
      })
      .catch(() => {
        Toast(`\n`)
      })
  }
}
</script>
