.login {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #ffffff;
  h1 {
    padding: 50px 24px 80px;
    font-size: 60px;
  }
  .van-cell {
    padding: 24px;
    :deep(*) {
      font-size: 30px;
    }
    :deep(.van-field__label) {
      color: #121212;
    }
    :deep(.van-field__error-message) {
      margin-top: 10px;
      font-size: 20px;
    }
    .send-btn {
      color: #666666;
    }
  }
  .submit-btn {
    height: 80px;
  }
}
</style>
